@font-face {
  font-family: "OpenSans-regular";
  src: local("OpenSans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-bold";
  src: local("OpenSans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.page__body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "OpenSans-regular", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 30px;
  color: #444444;
}

picture {
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
}

.page-main,
.page-catalog {
  min-height: 75vh;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 320px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
    width: 1150px;
  }
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 52px;
  padding: 16px 26px;
  font-family: "OpenSans-bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #444444;
  background-color: transparent;
  text-decoration: none;
  text-transform: uppercase;
  border: 2px solid #444444;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.title {
  padding: 0;
  margin: 0;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

.subtitle {
  padding: 0;
  margin: 0;
  font-size: 17px;
  font-weight: 700;
  line-height: 17px;
  text-transform: uppercase;
  color: #63d1bb;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.icon {
  display: block;
  color: #000000;
  opacity: 0.2;
}
.icon:hover, .icon:focus {
  color: #63d1bb;
  opacity: 1;
}
.icon:active {
  opacity: 0.3;
}

/* Control */
.control {
  position: relative;
  display: block;
  padding-left: 61px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .control {
    padding-left: 81px;
  }
}

/* Mark */
.control__mark {
  position: absolute;
  top: -8px;
  left: 0;
  width: 39px;
  height: 39px;
  border: 2px solid #444444;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .control__mark {
    top: -10px;
    width: 52px;
    height: 52px;
  }
}

/* Radio */
.control__input[type=radio] + .control__mark {
  position: absolute;
  top: -9px;
  left: 0;
  width: 41px;
  height: 41px;
  border: 2px solid #444444;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .control__input[type=radio] + .control__mark {
    top: -16.9px;
    left: 4px;
    width: 50px;
    height: 50px;
  }
}
@media (min-width: 1150px) {
  .control__input[type=radio] + .control__mark {
    top: -15.7px;
    left: 2.5px;
  }
}

.control__input[type=radio] + .control__mark {
  border-radius: 50%;
}

.control__input[type=radio]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 17.33px;
  height: 17.33px;
  background-color: #62D1BA;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .control__input[type=radio]:checked + .control__mark::before {
    top: 13px;
    left: 13px;
    width: 19.69px;
    height: 19.69px;
  }
}

/* Checkbox */
.control__input[type=checkbox] + .control__mark {
  position: absolute;
  content: "";
  top: -12px;
  left: 0;
  width: 38px;
  height: 38px;
  background-color: transparent;
  border: 2px solid #000;
}
@media (min-width: 768px) {
  .control__input[type=checkbox] + .control__mark {
    top: -14.9px;
    left: 1.4px;
    width: 50px;
    height: 50px;
  }
}
@media (min-width: 1150px) {
  .control__input[type=checkbox] + .control__mark {
    top: -19px;
  }
}

.control__input[type=checkbox]:checked + .control__mark::before {
  content: "";
  display: block;
  height: 38px;
  width: 38px;
  background-image: url("../../img/icons/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: 23px 16px;
  background-position: 6px center;
}
@media (min-width: 768px) {
  .control__input[type=checkbox]:checked + .control__mark::before {
    height: 52px;
    width: 52px;
    background-size: 32px 22px;
  }
}

/* Label */
.control__label {
  font-family: "OpenSans-bold", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 700;
  color: #000000;
  cursor: pointer;
}
.control__label:hover {
  color: #62D1BA;
}
.control__label:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .control__label {
    font-size: 19px;
  }
}

.control__input:focus ~ .control__mark,
.control__input:focus ~ .control__mark,
.control__input:active ~ .control__mark,
.control__input:active ~ .control__mark {
  border-color: #62D1BA;
}

.control:hover .control__mark {
  border-color: #62D1BA;
}

.control:active .control__mark {
  border-color: rgba(0, 0, 0, 0.3);
}

.input {
