.catalog {
  margin-top: 30px;

  @media (min-width: $tablet-width) {
    margin-top: 81px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 128px;
  }
}

.catalog__list {
  display: grid;
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  list-style: none;
  grid-row-gap: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 53px;
    grid-row-gap: 52px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 57px;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 48px;
  }
}

.catalog__item {
  border: 1px solid #E5E5E5;

  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
  }
}
